import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import logo from './images/relativity-formulae.svg';
import einstein from './images/einstein-1.jpg';

import '../node_modules/react-image-gallery/styles/css/image-gallery.css';
import './App.css';
import Form from 'react-bootstrap/Form';
const { ProgressBar, StatusBar } = require('@uppy/react');
const Uppy = require('@uppy/core');
const uppy = Uppy();
const robodog = require('@uppy/robodog');
const images = [
  {
    original: einstein
  },
  {
    original: './einstein-2.jpg'
  },
  {
    original: './einstein-3.jpg'
  },
  {
    original: './einstein-4.jpg'
  },
  {
    original: './einstein-5.jpg'
  },
];

//1. click einstein image you want
//2. generate quote somehow
//3. puts image and text into transloadit assembly
//4. assembly returns quote over image
//5. image put into its own frame etc

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      font: 'Ubuntu',
      fontSize: '12',
      message: 'Default statement'
    };
    this.uploadImage = this.uploadImage.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.handleFont = this.handleFont.bind(this);
    this.displayImage = this.displayImage.bind(this);
  }

  uploadImage(){
    let image = uppy.upload('/einstein-1.jpg', ()=>console.log(image));
    const resultPromise = robodog.upload(images,{
      params: {
        auth: { key: 'ee723b70d56f11e8b7809f4882ea3f24' },
      "steps": {
        ":original": {
          "robot": "/upload/handle"
        },
        "resized": {
          "use": [
            ":original"
          ],
          "robot": "/image/resize",
          "result": false,
          "height": 500,
          "imagemagick_stack": "v1.0.0",
          "resize_strategy": "fit",
          "width": 500,
          "zoom": false
        },
        "watermarked": {
          "use": [
            "resized"
          ],
          "robot": "/image/resize",
          "result": true,
          "imagemagick_stack": "v1.0.0",
          "text": [
            {
              "text": this.state.message,
              "size": 12,
              "font": this.state.font,
              "color": "#eeeeee",
              "valign": "bottom",
              "align": "right",
              "x_offset": 16,
              "y_offset": -10
            }
          ]
        }
      }
    }
    })
    resultPromise.then((bundle) =>{
      console.log(bundle.successful[0].uploadURL);
      return (
        <img src="http://tmp-eu-west-1.transloadit.com.s3-eu-west-1.amazonaws.com/15f060e0386811e99a7f97d0b7bfe016.png" alt="new" />
      );
    })
  }

  displayImage(){
    console.log('Display image');
  }

  handleSelect(e){
    e.preventDefault();
    let selection = e.target.value;
    this.setState({fontSize: selection});
  }

  handleFont(e){
    e.preventDefault();
    let selection = e.target.value;
    this.setState({font: selection});
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <h1>Choose Your Einstein</h1>
        <ImageGallery items={images} onClick={e=> this.uploadImage(e.preventDefault())}/>
        {/* <Button variant="outline-primary">Generate</Button> */}
        <Form>
          Font
          <Form.Control as="select" onClick={(e)=> this.handleFont(e)}>
            <option>Ubuntu</option>
            <option>Arial</option>
            <option>Comic Sans MS</option>
          </Form.Control>
        </Form>
        <Form>
          Font Size
          <Form.Control as="select" onClick={(e)=> this.handleSelect(e)}>
            <option>Small</option>
            <option>Medium</option>
            <option>Large</option>
          </Form.Control>
        </Form>
        <div>Icons made by <a href="https://www.freepik.com/" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0">CC 3.0 BY</a></div>
      </div>
    );
  }
}

export default App;
